*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
#my_projects{
    padding-top: 80px;
    padding-bottom: 120px;
}
#probtn
{
    border-radius: 25px;
    padding: 10px;
    border: 1px solid grey;
    font-weight: 600;
        background-color: #f8f9fa; 
        color: black; 
        transition: background-color 0.3s ease, color 0.3s ease; 
}
#probtn:hover {
    background-color: rgb(53, 52, 52); 
    color: white; 
   
}
.project_container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}
.project_box{
    width: 250px;
    height: 400px;
    border-radius: 30px;
    margin: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    border: 1px solid black;
    
   
} 
.project_img img{
    width:auto;
    height:250px;
    margin: 25px;
    object-fit: contain;
    align-items: center;
}

